<template>
  <select
    class="MSELECT"
    :value="modelValue"
    @change="
      $emit('update:modelValue', $event.currentTarget.value);
      checkInput();
    "
  >
    <template v-if="!optionType">
      <optgroup v-for="(optionGroup, key) in options" :key="key" :label="key">
        <option
          v-for="option in optionGroup"
          :key="option"
          :value="option.value"
          :disabled="option.disabled"
          :selected="selected == option.text"
        >
          {{ option.text }}
        </option>
      </optgroup>
    </template>
    <template v-else>
      <option
        v-for="option in options"
        :key="option.text"
        :value="option.value"
        :disabled="option.disabled"
        :selected="selected == option.text"
      >
        {{ option.text }}
      </option>
    </template>
    <slot></slot>
  </select>
</template>

<script>
export default {
  name: "MSelect",
  props: {
    // Custom attributes :
    required: Boolean,
    options: Array || Object,
    icon: String,
    selected: String,
    modelValue: String,
    id: {
      type: String,
      required: true,
    },
  },
  computed: {
    optionType() {
      return this.options instanceof Array;
    },
    formParent() {
      let parent = this.$parent;
      while (parent.$el.nodeName != "FORM" && parent.$el.nodeName != "HTML") {
        parent = parent.$parent;
      }
      return parent;
    },
  },
  methods: {
    checkInput() {
      if (this.required) {
        this.formParent.checkInput(this.id, true);
      }
    },
  },
  mounted() {
    if (this.required) {
      this.formParent.registerInput(this.id);
    }
  },
};
</script>

<style lang="scss" scoped>
.MSELECT {
  border: none;
  outline: none;
  border-radius: $ui_radius;
  padding: 4px 10px;
  background-color: changeOpacity("text", -0.92);
  font-size: 1em;
  font-family: inherit;
  transition: box-shadow 0.2s ease;
  &:focus {
    box-shadow: 0 0 4px 0 $color_accent;
  }
}
span {
  font-size: 0.84em;
  display: block;
  color: red;
}
</style>
