<template>
  <form class="MFORM" @submit.prevent>
    <slot></slot>
  </form>
</template>

<script>
export default {
  name: "MForm",
  data() {
    return {
      invalid_inputs_IDs: [],
    };
  },
  computed: {
    isAllInputsValid() {
      return this.invalid_inputs_IDs.length == 0 ? true : false;
    },
  },
  methods: {
    checkInput(inputId, inputState) {
      if (inputState) {
        this.invalid_inputs_IDs.splice(
          this.invalid_inputs_IDs.findIndex((i) => i == inputId),
          1
        );
      } else {
        this.invalid_inputs_IDs.push(inputId);
      }
    },
    registerInput(inputId) {
      this.invalid_inputs_IDs.push(inputId);
    },
  },
};
</script>

<style lang="scss" scoped></style>
