<template>
  <div class="MINPUT">
    <div>
      <i v-if="icon" :class="icon"></i>
      <input
        :class="`${icon ? 'iconed' : ''}`"
        :type="type"
        :placeholder="placeholder ? placeholder : ''"
        :value="modelValue"
        @input="
          $emit('update:modelValue', $event.currentTarget.value);
          firstInput = true;
        "
      />
    </div>
    <transition name="fade-back">
      <span v-if="pattern && !patternMatch && patternMsg && firstInput">
        {{ `* ${patternMsg}` }}
      </span>
    </transition>
  </div>
</template>

<script>
export default {
  name: "MINPUT",
  data() {
    return {
      firstInput: false,
    };
  },
  mounted() {
    if (this.pattern) {
      this.formParent.registerInput(this.id);
    }
  },
  watch: {
    patternMatch(matchState) {
      if (this.pattern) {
        this.formParent.checkInput(this.id, matchState);
      }
    },
  },
  computed: {
    patternMatch() {
      if (this.pattern) {
        return this.pattern.test(this.modelValue);
      } else return null;
    },
    formParent() {
      let parent = this.$parent;
      while (parent.$el.nodeName != "FORM" && parent.$el.nodeName != "HTML") {
        parent = parent.$parent;
      }
      return parent;
    },
  },
  props: {
    icon: String,
    placeholder: String,
    pattern: RegExp,
    patternMsg: String,
    modelValue: String,
    id: {
      type: String,
      required: true,
    },
    type: {
      type: String,
      default: "text",
      validator: (value) => {
        return (
          value == "email" ||
          value == "text" ||
          value == "password" ||
          value == "number" ||
          value == "date"
        );
      },
    },
  },
};
</script>

<style lang="scss" scoped>
div.MINPUT {
  height: fit-content;
  div {
    position: relative;
    i {
      display: inline-block;
      position: absolute;
      right: 16px;
      top: 50%;
      transform: translateY(-50%);
    }
    input {
      width: 100%;
      border: none;
      outline: none;
      border-radius: $ui_radius;
      padding: 4px 10px;
      background-color: changeOpacity("text", -0.92);
      font-size: 1em;
      font-family: inherit;
      caret-color: changeOpacity("accent", -0.5);
      transition: box-shadow 0.2s ease;
      &:focus {
        box-shadow: 0 0 4px 0 $color_accent;
      }
      &.iconed {
        padding-right: 48px;
      }
    }
  }
  span {
    font-size: 0.84em;
    display: block;
    color: red;
  }
}
</style>
