<script>
// import axios from "axios";
import MInput from "../components/MInput.vue";
import MSelect from "../components/MSelect.vue";
import MForm from "../components/MForm.vue";
import VueCropper from "vue-cropperjs";
import "cropperjs/dist/cropper.css";
export default {
  name: "register",
  components: { VueCropper, MInput, MForm, MSelect },
  watch: {
    inputImage(imgSrc) {
      const filePath = this.$refs.img_input.value;
      const allowedExtensions = /(\.jpg|\.png|\.jpeg|\.webp)$/i;
      if (allowedExtensions.test(filePath)) {
        this.parsedInputImage = URL.createObjectURL(imgSrc);
      } else {
        this.$refs.mpop.show({
          message: "   ",
          confirm: "",
        });
        this.inputImage = "";
      }
    },
  },
  data() {
    return {
      // App functioning data :
      levels: [
        { value: "beginner", text: "" },
        { value: "intermediate", text: "" },
        { value: "advanced", text: "" },
      ],
      languages: [
        { value: "en", text: "" },
        { value: "fr", text: "" },
        { value: "ru", text: "" },
        { value: "ge", text: "" },
      ],
      programming_languages: [
        { value: "c/c++", text: "C/C++" },
        { value: "c#", text: "C#" },
        { value: "java", text: "Java" },
        { value: "js", text: "JavaScript" },
        { value: "php", text: "PHP" },
        { value: "python", text: "Python" },
        { value: "dart", text: "Dart/flutter" },
        { value: "sql", text: "SQL" },
      ],
      courses: [
        "ICDL",
        "Electronics",
        "PLC",
        "Web development",
        "Mobile development",
        "Content writing",
        "A+",
        "Aurduino",
        "CCNA",
        "SCADA",
        "Graphic design",
        "Cyber security",
        "Robotics",
      ],
      inputImage: "",
      croppedImage: "", // in base64 format
      // Student Informations :
      studentsInfo: {
        personal: {
          picture: "",
          first_name: "",
          last_name: "",
          father_name: "",
          mother_name: "",
          gender: "male",
          birthdate: "",
          nationality: "",
          syrID: "",
          english_name: "",
        },
        professional: {
          yearOfRegister: "",
          currentYear: "",
          languages: [],
          programming_languages: [],
          main_courses: [],
          other_courses: "",
          experts: "",
        },
        contact: {
          email: "",
          phone: "",
          facebook: "",
          linkedin: "",
          telegram: "",
          github: "",
          behance: "",
          instagram: "",
        },
      },
    };
  },
  methods: {
    async sendData() {
      if (
        this.studentsInfo.personal.picture &&
        this.$refs.mform.isAllInputsValid &&
        this.studentsInfo.professional.yearOfRegister &&
        this.studentsInfo.professional.currentYear
      ) {
        this.$refs.waiting.show({
          title: " ",
        });
        let register;
        try {
          register = await fetch(
            "https://cae2022-db.herokuapp.com/students/add",
            {
              method: "POST",
              mode: "cors",
              headers: {
                "Content-Type": "application/json",
              },
              body: JSON.stringify(this.studentsInfo),
            }
          );
        } catch (err) {
          register = err.response;
        }
        await this.$refs.mpop.show({
          message: register.data || "    ",
          confirm: "",
        });
        this.$refs.waiting.respond(true);
      } else {
        this.$refs.mpop.show({
          message: "        .",
          confirm: "",
        });
      }
    },
    setInputImage(e) {
      this.croppedImage = "";
      this.inputImage = e.currentTarget.files[0];
    },
    cropImage() {
      // get image data for post processing, e.g. upload or setting image src
      this.croppedImage = this.$refs.cropper.getCroppedCanvas().toDataURL();
      this.studentsInfo.personal.picture = this.croppedImage.slice(
        this.croppedImage.indexOf(",")
      );
      // this.$refs.cropper.getCroppedCanvas().toBlob((blob) => {
      //   let i = new FormData();
      //   i.append("picture", blob);
      //   console.log(i);
      // });
    },
  },
};
</script>

